import fetch from './fetch'

// 
export const getCoins = data => fetch('/api/v1/webtransfer/getcoins', data, '')

// 
export const getTemplates = data => fetch('/api/v1/webtransfer/gettemplate', data, ', ')

// 
export const transferCommit = data => fetch('/api/v1/webtransfer/transfercommit', data, '', 'POST')

// web
export const getStatus = data => fetch('/api/v1/webtransfer/getcommitstatus', data)